<template>
  <div class="hello">
    <highcharts :options="options"></highcharts>
    <b-table striped hover
             :items="currencies"
             :fields="fields"
             :current-page="currentPage"
             :per-page="perPage">
      <template slot="index" slot-scope="data">
        {{ relativeIndex(data.index) }}
      </template>
      <template slot="name" slot-scope="data">
        <span v-if="coinList[data.item.symbol]">
          <img class="logo" :src="`https://cryptocompare.com${coinList[data.item.symbol].ImageUrl}`" />
        </span>
        {{ data.value }}
      </template>
      <template slot="market_cap_eur" slot-scope="data">
        {{ data.value | currency('€') }}
      </template>
      <template slot="price_eur" slot-scope="data">
        {{ data.value | currency('€') }}
      </template>
      <template slot="24h_volume_eur" slot-scope="data">
        {{ data.value | currency('€') }}
      </template>
      <template slot="total_supply" slot-scope="data">
        {{ data.value | currency('', 0) }}
      </template>
      <template slot="percent_change_24h" slot-scope="data">
        <span :class="data.value > 0 ? 'up':'down'">{{data.value}} %</span>
      </template>
      <template slot="percent_change_1h" slot-scope="data">
        <span :class="data.value > 0 ? 'up':'down'">{{data.value}} %</span>
      </template>
    </b-table>
    <b-pagination
      align="center"
      :total-rows="totalRows"
      :per-page="perPage"
      v-model="currentPage">
    </b-pagination>
  </div>
</template>

<script>
import axios from 'axios';

const fields = [
  { key: 'index', sortable: false, label: '#' },
  { key: 'name', sortable: true, label: 'Name' },
  { key: 'market_cap_eur', sortable: true, label: 'Market Cap' },
  { key: 'price_eur', sortable: true, label: 'Price' },
  { key: '24h_volume_eur', sortable: true, label: 'Volume (24h)' },
  { key: 'total_supply', sortable: true, label: 'Circulating Supply' },
  { key: 'percent_change_24h', sortable: true, label: 'Change (24h)' },
  { key: 'percent_change_1h', sortable: true, label: 'Change (1h)' },
];

const options = {
  chart: {
    type: 'pie',
  },
  title: {
    text: 'Top 10 currencies by Market Cap',
  },
  tooltip: {
    pointFormat: '{series.name}: <b>{point.percentage:.1f}%</b>',
  },
  series: [{
    name: 'Market Cap',
    colorByPoint: true,
    data: [],
  }],
};

export default {
  data() {
    return {
      fields,
      options,
      coinList: {},
      currencies: [],
      errors: [],
      currentPage: 1,
      perPage: 10,
      totalRows: 500,
    };
  },
  methods: {
    relativeIndex(index) {
      return (index + 1) + ((this.currentPage - 1) * this.perPage);
    },
    loadData() {
      axios.get('https://api.coinmarketcap.com/v1/ticker/?convert=EUR&limit=100')
        .then((response) => {
          this.currencies = response.data;
          this.options.series[0].data = this.currencies.slice(0, 10)
            .map((element, index) => ({
              name: element.name,
              y: Number(element.market_cap_eur),
              sliced: !index,
            }));
        })
        .catch((error) => {
          this.errors.push(error);
        });
    },
  },
  created() {
    this.loadData();

    axios.get('https://min-api.cryptocompare.com/data/all/coinlist')
      .then((response) => {
        this.coinList = response.data.Data;
      })
      .catch((error) => {
        this.errors.push(error);
      });

    setInterval(() => {
      this.loadData();
    }, 60000);
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .up {
    color: green;
  }
  .down {
    color: red;
  }
  img.logo {
    height: 20px;
    width: 20px;
  }
</style>



